.top{
    width: 100%;
    height: 50px;
    background-color: white;
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    font-family: 'Josefin Sans', sans-serif;
}
.topleft,.topright{
    flex: 3;
    display: flex;
    align-items: center;
    justify-content: center;
}
.topcenter{
    flex: 6;
}

.topIcon{
    font-size: 40px;
    margin-right: 10px;
    color: #444;
    cursor: pointer;
}
.topImg{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
}
.toplist{
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none;
    font-style: uppercase;
}
.toplistitem{
    margin-right: 20px;
    font-size: 18px;
    font-weight: 300;
    cursor: pointer;
}
.topSearch{
    color: #444;
    font-size: 18px;
    cursor: pointer;
    margin-left: 15px;
}